/* SCSS Variables
---------------------------------------------- */
$bodycolor: rgba(0, 0, 0, 0.75);
$yellow: #ECEA71;
$lightgrey: #f1f1f1;
$fontbold: arial, helvetica, sans-serif;
$fontregular: arial, helvetica, sans-serif;
$s-space: 0.75rem;
$m-space: 1.2rem;
$l-space: 2.5rem;
$xl-space: 5rem;

/* ──────────────────────────────────────────────
 Modern Base Reset (2025) Inspired by Andy Bell, Josh Comeau & modern‑css.dev
 ────────────────────────────────────────────── */
/* 1. Use border‑box everywhere */
*,
*::before,
*::after {
 box-sizing: border-box;
}
/* 2. Remove default block margins */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
 margin: 0;
}
/* 3. Typography defaults */
body {
 min-height: 100vh;
 line-height: 1.5;
 -webkit-font-smoothing: antialiased;
 text-rendering: optimizeSpeed;
}

/* 4. Lists → unstyled unless author opts‑in with role=list */
ul[role='list'],
ol[role='list'] {
 list-style: none;
 padding: 0;
}

/* 5. Media elements */
img,
picture,
video,
canvas,
svg {
 max-width: 100%;
}

/* 6. Make form elements inherit fonts */
input,
button,
textarea,
select {
 font: inherit;
}

/* 7. Reduce animation for motion‑sensitive users */
@media (prefers-reduced-motion: reduce) {

 *,
 *::before,
 *::after {
 animation-duration: 0.01ms !important;
 animation-iteration-count: 1 !important;
 transition-duration: 0.01ms !important;
 scroll-behavior: auto !important;
 }
}

/* 8. Remove Chrome’s blue button focus outline but keep accessibility outline */
button:focus-visible {
 outline: 2px solid currentColor;
 outline-offset: 2px;
}

button:focus:not(:focus-visible) {
 outline: none;
}

/* 9. Tables: collapse borders by default */
table {
 border-collapse: collapse;
 border-spacing: 0;
}

/* 10. “hidden” attribute always hides */
[hidden] {
 display: none !important;
}

/* 11. Make anchor highlight behaviour consistent */
a {
 text-decoration-thickness: 0.08em;
 text-underline-offset: 0.15em;
}

/* BASE STYLES
----------------------------------------------
---------------------------------------------- */
body,
html {
 margin: 0;
 padding: 0;
 height: 100%;
}

html {
 font-size: 18px;
 font-family: $fontregular;
}

body {
 color: $bodycolor;
 line-height: 1.6;

}

* {
 margin: 0;
 padding: 0;
}

*,
:after,
:before {
 box-sizing: border-box;
}


hr {
 display: block;
 height: 2px;
 border: 0;
 border-top: 2px solid $bodycolor;
 margin: 0 0 $m-space;
 padding: 0;
}

p {
 margin-bottom: 1.2em;
