 margin-bottom: $m-space;
}

h3 {
 font-weight: 700;
 font-size: 1.3em;
 margin-bottom: $m-space;
}

h4 {
 font-size: 1em;
 text-transform: uppercase;
 font-family: $fontregular;
 font-weight: 400;
}

h5 {
 font-size: 1em;
 font-weight: 700;
 text-transform: uppercase;
 margin-bottom: 0.25em;
}

h6 {
 font-size: 1em;
}

.tc {
 text-align: center;
}

.tl {
 text-align: left;
}

.tr {
 text-align: right;
}

img {
 margin-bottom: $m-space;
 margin-bottom: 0;
}

ul {
