// HEADER AND NAVIGATION STYLING //
//---------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------- //

/* HEADER
---------------------------------------------- */
/* Animate Navigation Styling. Credit for the full code from which this functionality was derived goes to Alex Cican */

header {
 width: 100%;
 padding: 0;
 position: fixed;
 top: 0;
 right: 0;
 z-index: 9999;
}

nav {
 color: $bodycolor;
 position: fixed;
 top: 0;
 right: 0;
 z-index: 10000;
 cursor: pointer;
 transform: translate(0, 0);
 transition: transform 0.4s, height 0.3s, background 0.4s;
 animation: firstFadeInAndDrop 0.5s;

 a {
 text-decoration: none;
 border-bottom: 0;
 }
}

/* Animate Drop Down of Menu */
@keyframes firstFadeInAndDrop {
 0% {
 transform: translate(0, -54px);
 }

 100% {
 transform: translate(0, 0);
 }
}

/* positions navigation content */
#navigation {
 transform: translate(0, -1000px);
 opacity: 0;
 display: none;
 transition: transform 5s, opacity 7s;
 color: white;
 margin: 0;
 padding: $xl-space $xl-space $s-space $xl-space;

 .content {
 width: 100%;
 }

 .span12 {
 margin-bottom: $l-space;
 margin-top: 1em;
 /* nav spacing*/
 }

 a:hover {
 text-decoration: none;
 border-bottom: 0.1em solid white;
 }

 h1 {
 text-align: left;
 color: white;
 width: 100%;
 display: inline-block;
 line-height: 1;
 margin: $s-space 0;
 font-size: 2.75em;

 a {
 color: white;
 }
 }
}

/* Bar Transition to X */
#nav-container {
 position: fixed;
 top: 0;
 right: 0;
 margin: 0;
 z-index: 999;
 background: white;
 padding: $m-space;
}

.toggle-icon {
 position: relative;
 display: block;
 width: 100%;
 margin: 0;
 padding: 0;
 cursor: pointer;
 transition: margin 600ms;
}

/* Positiioning Bars */
$mathnum: 0.3em; // Use this to size main bars

.toggle-icon .bar {
 display: block;
 width: calc(#{$mathnum} * 10);
 height: calc(#{$mathnum} * 1);
 margin-bottom: calc(#{$mathnum} * 2);
 transition-duration: 600ms;
 transition-property: transform, margin, border-right-color, box-shadow;
 background: black;
}

.toggle-icon .bar:nth-of-type(3) {
 margin-bottom: 0;
}

.toggle-icon .bar:nth-of-type(2) {
 transition: opacity 300ms;
}

.pushed .bar {
 background: #ECEA71;
}

.pushed .bar:nth-of-type(2) {
 opacity: 0;
}

.pushed .bar:nth-of-type(1) {
 transform: translateY(calc(#{$mathnum} * 3)) rotate(-135deg);
}

.pushed .bar:nth-of-type(3) {
 transform: translateY(calc(#{$mathnum} * -3)) rotate(-45deg);
}

/* increases menu width & height NAV BACK COLOUR */
nav.expanded {
 width: 100%;
 height: 100%;
 cursor: default;
 background: rgba(0, 0, 0, 1);
 opacity: 0.98;
 z-index: 997;
 overflow: scroll;

 body {
 filter: blur(5px);
 }
 /* shows navigation */
 #navigation {
 display: block;
 margin: 0;
 opacity: 1;
 position: relative;
 transform: translate(0, 0);
 width: 100%;
 z-index: 998;
 }
 #nav-container {
 position: fixed;
 background: transparent;
 }
}

.ae-dropdown {
 background: white;
 color: $bodycolor;
}

/* LI begin hidden */
.ae-hide {
 display: none;
}

/* Hide selected tag from main list */
li.is-checked {
 display: none !important;
}

/* END project filter */

/* BACKGROUND IMAGE STYLING ELEMENTS ----------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
/* Projects Grid */
.project-grid {
 margin: 0;
 padding: 0;
